:root {
  --light-green: hsl(120, 38%, 85%);
  --light-tan: hsl(7, 31%, 95%);
  --faded-pink: hsl(326, 43%, 80%);
  --pastel-pink1: hsl(303, 58%, 74%);
  --pastel-pink2: hsl(316, 69%, 61%);
  --dark-pink: hsl(318, 55%, 9%);
}

:root {
  --font-ibm-plex: "IBM Plex Mono", system-ui;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-ibm-plex);
  background: var(--light-tan);
  color: var(--dark-pink);
}

h1,
h2,
h3 {
  margin: 0.9375rem;
  margin-bottom: 0;
  line-height: 1.1;
  letter-spacing: 0.25rem;
}

h1 {
  font-size: 2.5em;
  text-transform: uppercase;
  font-weight: 500;
  background-color: var(--faded-pink);
  max-width: fit-content;
}
@media (min-width: 64em) {
  h1 {
    font-size: 1.75rem;
  }
}

p {
  margin: 1.5625rem;
  font-size: 0.875rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

main {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  width: 100%;
  display: flex;
  flex-wrap: nowrap;
}
@media (min-width: 64em) {
  main {
    padding: 0;
  }
}

.section {
  height: 100vh;
}

.header {
  margin-top: 1.625rem;
}
@media (min-width: 40em) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--dark-primary-text-color);
}
.left-pane {
  max-width: 31.25rem;
  border-right: 0.0625rem solid var(--dark-pink);
}
.left-pane__about {
  margin-top: 12.5rem;
}
.left-pane__footer {
  border-top: 0.0625rem solid var(--dark-pink);
}

.right-pane {
  width: 100%;
}