html {
    font-size: 100%;
    box-sizing: border-box; //Makes sure padding is included in total width
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

body {
    margin: 0;
    padding: 0;
    font-family: var(--font-ibm-plex);
    background: var(--light-tan);
    color: var(--dark-pink);
}